.statistics {
  display: block;
  width: 376px;
  align-items: center;
  margin: 50px auto;
  border: 1px solid whitesmoke;
  box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 1px 3px rgba(0, 0, 0, 0.12);
}
.title {
  margin: 0;
  padding: 20px;
  text-align: center;
  background-color: #fff;
  color: rgb(70, 70, 70);
  text-transform: uppercase;
  border-bottom: 1px solid whitesmoke;
}

.statList {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
}

.item {
  padding: 15px;
  border: 1px solid bisque;
  display: grid;
  text-align: center;
}

.item:nth-child(2n + 1) {
  background-color: rgba(11, 127, 173, 0.8);
}
.item:nth-child(2n) {
  background-color: rgba(213, 12, 231, 0.8);
}

.label {
  margin-bottom: 5px;
  color: whitesmoke;
  font-style: 14px;
}

.percentage {
  color: whitesmoke;
  font-size: 22px;
}
